<template>
    <div class="scroll-progress-bar" v-bind:style="{ width: progressState + '%' }"></div>
</template>

<script>
  export default {
    name: 'pageScrollProgressBar',
    data () {
      return {
        pageHeight: 0,
        progressState: 0,
        topPosition: 0,
        doc: document.documentElement
      }
    },
    methods: {
      handleScroll () {
        this.progressState = (((window.pageYOffset || this.doc.scrollTop) - (this.doc.clientTop || 0)) / (this.pageHeight - window.innerHeight)) * 100
      }
    },
    created () {
      window.addEventListener('scroll', this.handleScroll)
    },
    destroyed () {
      window.removeEventListener('scroll', this.handleScroll)
    },
    mounted () {
      this.topPosition = (window.pageYOffset || this.doc.scrollTop) - (this.doc.clientTop || 0)
      this.pageHeight = document.getElementById('app').offsetHeight
      this.progressState = (this.topPosition / (this.pageHeight - window.innerHeight)) * 100
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .scroll-progress-bar {
    position: fixed;
    top: 0px;
    left: 0px;
    height: 5px;
    display: block;
    z-index: 10000;
    background-color: rgb(63, 191, 127);
  }
</style>



