<template>
  <div>
    <div id="menu-bar" v-bind:class="sticky">
      <ul class="menu-list">
        <li v-for="menu in menuElements" v-bind:class="{ 'active': menu == actualPosition }">
          <a v-bind:href="menu.href" v-on:click="clickfunc(menu)">{{ menu }}</a>
        </li>
      </ul>
    </div>
    <nav class="navbar navbar-default navbar-fixed-top">
      <div class="container-fluid">
        <!-- Brand and toggle get grouped for better mobile display -->
        <div class="navbar-header">
          <button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1" aria-expanded="false">
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <a class="navbar-brand" href="#">MZdunski.net</a>
        </div>

        <!-- Collect the nav links, forms, and other content for toggling -->
        <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
          <ul class="nav navbar-nav">
            <li  v-for="menu in menuElements">
              <a v-bind:href="menu.href" v-on:click="clickfunc(menu)">{{ menu }}</a>
            </li>
          </ul>
        </div><!-- /.navbar-collapse -->
      </div><!-- /.container-fluid -->
    </nav>
  </div>
</template>

<script>
export default {
  name: 'menuBar',
  props: [ 'menuElements', 'actualPosition' ],
  data () {
    return {
      sticky: 'absolute',
      wHeight: 0,
      spyHeight: 0
    }
  },
  methods: {
    handleScroll () {
      if (window.scrollY > this.wHeight && this.sticky !== 'fixed') {
        this.sticky = 'fixed'
      }
      if (window.scrollY <= this.wHeight && this.sticky === 'fixed') {
        this.sticky = 'absolute'
      }
    },
    clickfunc (target) {
      this.$SmoothScroll(document.getElementById(target))
    }
  },
  created () {
    this.wHeight = window.innerHeight
    window.addEventListener('scroll', this.handleScroll)
    window.addEventListener('resize', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
    window.removeEventListener('resize', this.handleScroll)
  },
  mounted () {
    if (window.scrollY > this.wHeight && this.sticky !== 'fixed') {
      this.sticky = 'fixed'
    }
    if (window.scrollY < this.wHeight && this.sticky !== 'absolute') {
      this.sticky = 'absolute'
    }
  }
}
</script>

<style scoped>

  #menu-bar {
    right: 2%;
    background-color: rgb(63, 191, 127);
    width: 80px;
    border: none;
    border-radius: 0px 0px 7px 7px;
    display: none;
  }

  @media (min-width: 768px) {
    #menu-bar {
      display: block;
      }
    .navbar {
      display: none;
    }
  }

  .absolute {
    position: absolute;
    top: 100%;
    z-index: 100;
  }

  .fixed {
    position: fixed;
    top: 0px;
  }

  .menu-list {
    list-style: none;
    padding: 0px;
    margin: 0px;
  }

  .menu-list > li {
    height: 60px;
    line-height: 60px;
    padding: 0px;
    margin: 0px;
    transition: 0.5s;
  }

  .navbar-nav > li > a {
    cursor: pointer;
  }

  .navbar-nav > li > a:hover {
    background-color: silver;
  }

  .menu-list > li:last-child,
  .menu-list > li:last-child > a {
    border-radius: 0px 0px 7px 7px;
  }

  .menu-list > li.active,
  .menu-list > li.active:last-child {
    background-color: black;
    transition: 0.5s;
  }

  .menu-list > li > a {
    text-decoration: none;
    font-weight: bold;
    font-size: 10px;
    color: #FFF;
    transition: 0.5s;
    display: block;
    cursor: pointer;
  }

  .menu-list > li > a:first-letter {
    text-transform: uppercase;
  }

  .menu-list > li > a:active {
    background-color: #000;
    transition: 0.5s;
  }

  .menu-list > li > a:hover {
    background-color: rgba(231, 227, 218, 1);
    color: #000;
    transition: 0.5s
  }
</style>



