<template>
  <div id="app">
    <router-view></router-view>
    <page-background></page-background>
    <page-scroll-progress-bar></page-scroll-progress-bar>
  </div>
</template>

<script>
import PageBackground from './components/PageBackground.vue'
import PageScrollProgressBar from './components/PageScrollProgressBar.vue'

export default {
  components: {
    PageBackground,
    PageScrollProgressBar
  },
  name: 'app',
  data () {
    return {
      pageHeight: 0
    }
  },
  mounted () {
    this.pageHeight = document.getElementById('app').offsetHeight
  }
}
</script>

<style>

  html, body, div {
    padding: 0px;
    margin: 0px;
  }

  #app {
    padding: 0px;
    margin: 0px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  .container {
    max-width: 1240px;
    padding: 40px 10px;
  }

  @media (min-width: 768px) {
    .container {
      max-width: 1240px;
      padding: 40px 140px;
    }
  }

  hr {
    border-color: #e0e0e0;
  }
</style>



