<template>
  <div id="PageCoreElement">
    <menu-bar :menu-elements='menuElements' :actual-position='actualScrollPosition' ignore></menu-bar>
    <intro-label id="intro" class="full-window centered" ignore></intro-label>
    <profile id="profile" class="bg-white"></profile>
    <experience id="experience" class="bg-grey"></experience>
    <abilities id="abilities" class="bg-white"></abilities>
    <projects id="projects" class="bg-grey"></projects>
    <contact id="contact" class="bg-dark-grey full-window"></contact>
  </div>
</template>

<script>
import menuBar from './Menu.vue'
import IntroLabel from './IntroLabel.vue'
import Profile from './Profile.vue'
import Experience from './Experience.vue'
import Abilities from './Abilities.vue'
import Projects from './Projects.vue'
import Contact from './Contact.vue'

export default {
  name: 'Core',
  components: {
    IntroLabel,
    Profile,
    Experience,
    Abilities,
    Projects,
    Contact,
    menuBar
  },
  data () {
    return {
      pageHeight: 0,
      menuElements: [],
      elementsOffset: [],
      actualScrollPosition: ''
    }
  },
  methods: {
    getElementsOffset () {
      for (var i = 0; i < this.menuElements.length; i++) {
        this.elementsOffset[i] = {element: this.menuElements[i], offsetTop: document.getElementById(this.menuElements[i]).offsetTop}
      }
      this.actualScrollPosition = this.menuElements[0]
      console.log(this.actualScrollPosition)
      this.checkActualPosition()
    },
    checkActualPosition () {
      var actualPos
      this.actualScrollPosition = this.elementsOffset.map(function (item, index) {
        if (item.offsetTop < (window.scrollY + 200)) {
          actualPos = item.element
        }
      })
      if ((this.actualScrollPosition !== actualPos) && (actualPos !== undefined)) {
        this.actualScrollPosition = actualPos
      } else {
        this.actualScrollPosition = this.menuElements[0]
      }
    }
  },
  created () {
    window.addEventListener('scroll', this.checkActualPosition)
    window.addEventListener('resize', this.checkActualPosition)
  },
  destroyed () {
    window.removeEventListener('scroll', this.checkActualPosition)
    window.removeEventListener('resize', this.checkActualPosition)
  },
  mounted () {
    var rdElements = this.$children
    for (var i = 0; i < rdElements.length; i++) {
      if (rdElements[i].$el.hasAttribute('id') && !rdElements[i].$el.hasAttribute('ignore')) {
        this.menuElements.push(rdElements[i].$el.getAttribute('id'))
      }
    }
    setTimeout(() => (this.getElementsOffset()), 300)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

  .full-window {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    background-color: rgba(14, 14, 14, 0.4);
    }

  .centered {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
  }

  .bg-grey {
    background-color: rgba(252, 248, 248, 1);
  }

  .bg-white {
    background-color: rgba(255, 255, 255, 1);
  }

  .bg-dark-grey {
    background-color: #474747;
  }
</style>



